@-webkit-keyframes triviaAnimation {

  0% {
    -webkit-transform: rotate(20deg) translate(100px, -15px);
            transform: rotate(20deg) translate(100px, -15px); }

  25% {
    -webkit-transform: rotate(0) translate(0, -45px);
            transform: rotate(0) translate(0, -45px); }

  50% {
    -webkit-transform: rotate(-20deg) translate(-100px, -15px);
            transform: rotate(-20deg) translate(-100px, -15px); }

  75% {
    -webkit-transform: rotate(0) translate(0, -45px);
            transform: rotate(0) translate(0, -45px); }

  100% {
    -webkit-transform: rotate(20deg) translate(100px, -15px);
            transform: rotate(20deg) translate(100px, -15px); } }

@keyframes triviaAnimation {

  0% {
    -webkit-transform: rotate(20deg) translate(100px, -15px);
            transform: rotate(20deg) translate(100px, -15px); }

  25% {
    -webkit-transform: rotate(0) translate(0, -45px);
            transform: rotate(0) translate(0, -45px); }

  50% {
    -webkit-transform: rotate(-20deg) translate(-100px, -15px);
            transform: rotate(-20deg) translate(-100px, -15px); }

  75% {
    -webkit-transform: rotate(0) translate(0, -45px);
            transform: rotate(0) translate(0, -45px); }

  100% {
    -webkit-transform: rotate(20deg) translate(100px, -15px);
            transform: rotate(20deg) translate(100px, -15px); } }

